<template>
    <div class="server_service">
        <!-- 头部 -->
        <!-- <header style="height:100px;background-color:#545c64;line-height:100px">
            <span style="margin-left:50px;color:white;cursor:pointer" @click="goIndex">返回首页</span>
        </header> -->
        <!-- 添加服务 -->
        <div style="width:100%;text-align:right;height:80px;line-height:80px">
            <span style="float:left;font-size:20px;font-weight:bold;margin-left:10px">设备服务</span>
            <el-button type="primary" style="margin-right:20px" @click="addService">添加</el-button>
        </div>   
        <!-- 设备服务信息 -->
        <el-table
        :data="tableData"
        border
    style="width: 100%;">
    <el-table-column
      prop="sName"
      label="服务名"
      width="180"
      >
    </el-table-column>
    <el-table-column
      prop="sType"
      label="服务类型">
    </el-table-column>
    <!-- <el-table-column
      prop="sMessage"
      label="自定义配置信息">
    </el-table-column> -->
    <el-table-column
      prop="autoNotice"
      label="报警通知">
    </el-table-column>
    <el-table-column
      prop="chargePerson"
      label="负责人">
    </el-table-column>
    <el-table-column
      prop="autoRepair"
      label="是否自动修复">
    </el-table-column>
    <!-- <el-table-column
      prop="tMethod"
      label="自动检测方法">
    </el-table-column>
    <el-table-column
      prop="rMethod"
      label="自动修复方法">
    </el-table-column> -->

    <el-table-column
      prop="methods"
      label="操作"
       width="250"
      >
      <template slot-scope="scope" >

          <el-button
          size="small"
          @click="handleEdit(scope.$index, scope.row)">修改</el-button>

        <el-button
          size="small  "
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>

          <el-button
          size="small  "
          type="primary"
           @click="goTest(scope.$index, scope.row)">管理</el-button>
        </template>
    </el-table-column>
    </el-table>
    <!-- 修改信息会话框 -->
    <el-dialog title="修改信息" :visible.sync="dialogFormVisible" width="30%">
    <el-form :model="form">
      <!-- <el-form-item label="修改项目" :label-width="formLabelWidth">
        <el-select v-model="form.region" placeholder="请选择修改项目">
          <el-option  v-for="(item,index) in keys" :key="index" :value="item" :label="item"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="修改值" :label-width="formLabelWidth">
        <el-input v-model="form.name" autocomplete="off"></el-input>
      </el-form-item> -->

      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="服务名称">
          <el-input v-model="form.sName"></el-input>
        </el-form-item>
        
          <el-form-item label="服务类型">
              <el-input v-model="form.sType"></el-input>
          </el-form-item>

        <el-form-item label="报警通知">
          <el-switch v-model="form.autoNotice"></el-switch>
        </el-form-item>

        <el-form-item label="自动修复">
          <el-switch v-model="form.autoRepair"></el-switch>
        </el-form-item>


            <el-form-item label="负责人">
                  <el-select v-model="form.chargePerson" placeholder="请选择负责人">
                  <el-option :label="item.cName" :value="item.cId" v-for="(item,index) in person" :key="index"></el-option>
                  </el-select>
              </el-form-item>


          <!-- <el-form-item label="自动检测方法">
              <el-select v-model="form.testMethodIds" placeholder="请选择方法">
              <el-option :label="item.tName" :value="item.cId" v-for="(item,index) in testList" :key="index"></el-option>
              </el-select>
          </el-form-item> -->

          <!-- <el-form-item label="自动修复方法">
          <el-checkbox-group v-model="form.repairMethodIds">
            <el-checkbox :label="item.rName" name="rId" v-for="(item,index) in repairList" :key="index"></el-checkbox>
            
          </el-checkbox-group>
        </el-form-item> -->
        <div>
            <div style="text-align:center">自定义配置信息</div>
            属性名:<el-input v-model="obj.key" placeholder="请输入内容"></el-input>
            属性值<el-input v-model="obj.value" placeholder="请输入内容"></el-input>
        </div>
      </el-form>

      <span>报警通知、自动修复要填写（0或者1）否则提交不成功</span>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible = false">取 消</el-button>
      <el-button type="primary" @click="updateUser">确 定</el-button>
    </div>
</el-dialog>


<!-- 添加服务会话框 -->
<el-dialog
  title="新增服务"
  :visible.sync="dialogVisible"
  width="30%"
  >
  
    <el-form ref="form" :model="newform" label-width="80px">
  <el-form-item label="服务名称">
    <el-input v-model="newform.sName"></el-input>
  </el-form-item>
  
    <el-form-item label="服务类型">
        <el-input v-model="newform.sType"></el-input>
    </el-form-item>

  <el-form-item label="报警通知">
    <el-switch v-model="newform.autoNotice"></el-switch>
  </el-form-item>

  <el-form-item label="自动修复">
    <el-switch v-model="newform.autoRepair"></el-switch>
  </el-form-item>


      <el-form-item label="负责人">
            <el-select v-model="newform.chargePerson" placeholder="请选择负责人">
            <el-option :label="item.cName" :value="item.cId" v-for="(item,index) in person" :key="index"></el-option>
            </el-select>
        </el-form-item>


    <el-form-item label="自动检测方法">
        <el-select v-model="newform.testMethodIds" placeholder="请选择方法">
         <el-option :label="item.tName" :value="item.tId" v-for="(item,index) in testList" :key="index"></el-option>
        </el-select>
    </el-form-item>

    <el-form-item label="自动修复方法">
    <el-checkbox-group v-model="newform.repairMethodIds">
      <el-checkbox :label="item.rName" name="rId" v-for="(item,index) in repairList" :key="index"></el-checkbox>
      
    </el-checkbox-group>
  </el-form-item>
  <div>
      <div style="text-align:center;background-color:#545c64;height:50px;line-height:50px;color:white">自定义配置信息</div>
      <div style="margin-top:10px">属性名:<el-input v-model="obj.key" placeholder="请输入内容"></el-input></div>
      <div style="margin-top:10px">属性值<el-input v-model="obj.value" placeholder="请输入内容"></el-input></div>
  </div>
</el-form>

  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="submit">添加</el-button>
  </span>
</el-dialog>

    </div>
</template>

<script>
export default {
    name:'ServerService',
    data(){
        return{
            tableData:[],
            obj:{
                key:'',
                value:''
            },
            //添加会话框
            dialogVisible:false,
            //修改会话框的显示控制
            dialogFormVisible:false,
            formLabelWidth:'120px',
            //修改提交数据
            form: {
                sName: '',
                sType: '',
                autoNotice: false,
                autoRepair: false,
                chargePerson: '',
                testMethodIds: '',
                repairMethodIds: []
            },
            //属性值数组
            keys:[],
            //提交信息表单
            newform:{
                sName: '',
                sType: '',
                autoNotice: false,
                autoRepair: false,
                chargePerson: '',
                testMethodIds: '',
                repairMethodIds: []
            },
            //负责人
            person:[],
            //检测方法
            testList:[],
            //当前服务内容
            serviceContent:{},
            //修复方法列表
            repairList:[],
            eId:''
        }
    },
    methods:{
        //返回
        goIndex(){
            this.$router.push('/')
        },
        //修改
        handleEdit(index, row) {
            this.index = index
            this.dialogFormVisible = true
            this.keys = Object.keys(row)
            this.serviceContent = row
            this.form = row
            let arr = JSON.parse(this.form.sMessage)
            this.obj = arr[0]
        },
        //删除
        handleDelete(index, row) {
          this.$axios.delete('http://192.168.1.155:8888/serverController/'+row.sId).then((msg)=>{
            if (msg.data) {
              this.$message({
                  message: '恭喜你，删除成功',
                  type: 'success'
                });
                window.location.reload()
            }else{
              this.$message.error('删除失败');
            }
          })
        },
        //修改提交信息
        updateUser(){
            this.dialogFormVisible = false
            if (this.form.autoNotice) {
              this.form.autoNotice = 1
            }else{
              this.form.autoNotice = 0
            }

            if (this.autoRepair) {
              this.form.autoRepair = 1
            }else{
              this.form.autoRepair = 0
            }
            this.$axios.put('http://192.168.1.155:8888/serverController',this.form).then((msg)=>{
                if (msg.data) {
                    this.$message({
                        message: '恭喜你，修改成功',
                        type: 'success'
                    });
                    window.location.reload()
                }else{
                    this.$message.error('修改失败');
                }
            })
        },
        //添加服务会话框显示
        addService(){
            this.dialogVisible = true
        },
        //添加服务
        submit(){
            this.newform.sMessage = JSON.stringify([this.obj])
            this.newform.repairMethodIds = JSON.stringify(this.newform.repairMethodIds)
            if (this.newform.autoNotice) {
              this.newform.autoNotice = 1
            }else{
              this.newform.autoNotice = 0
            }
            if (this.newform.autoRepair) {
              this.newform.autoRepair = 1
            }else{
              this.newform.autoRepair = 0
            }
            this.newform.testMethodIds = JSON.stringify([this.newform.testMethodIds])
            this.newform.eId = this.eId
            this.$axios.post('http://192.168.1.155:8888/serverController/insertServer',this.newform).then((msg)=>{
              if (msg.data) {
                    this.$message({
                        message: '恭喜你，添加成功',
                        type: 'success'
                    });
                    this.dialogVisible = false
                    window.location.reload()
                }else{
                    this.$message.error('添加失败');
                }
            })
        },
        //管理检测与修复
        goTest(index,row){
            this.$router.push('/test').then(()=>{
              // console.log(row)
            })
        }
    },
    created(){
        //设备信息
        this.eId = localStorage.getItem('eId')
        //获取负责人列表
        this.$axios.get('http://192.168.1.155:8888/CPController').then((msg)=>{
            this.person = msg.data
        })
        //获取检测列表
        this.$axios.get('http://192.168.1.155:8888/TMController').then((msg)=>{
            console.log(msg)
          this.testList = msg.data
        })
        //获取修复方法列表
        this.$axios.get('http://192.168.1.155:8888/RMController').then((msg)=>{
            console.log(msg)
          this.repairList = msg.data
        })
    },
    mounted(){
        //获取服务列表
        let arr = []
        this.$axios.get('http://192.168.1.155:8888/serverController').then((msg)=>{
          for (let i = 0; i < msg.data.length; i++) {
            if (msg.data[i].eId == this.eId) {
              arr.push(msg.data[i])
            }
          }
          this.tableData = arr
        })
    }
}
</script>

<style scoped>

</style>>



